    --logo-margin: 0 0 2.5rem;
    --page-top-padding: 11rem 0 0;
    --page-top-paragraph: 2.1rem;
    --screenshots-margin-bottom: 5rem;
    --explainer-text-lead: 2.4rem;
    --key-features-padding: 7rem 0;
    --key-features-title: 3.2rem;
    --footer-text-margin: 8rem 0 0; } }

