.page-top {
  background: var(--blue);
  color: white;
  padding: var(--page-top-padding);
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 0) 100%), var(--blue); }
  .page-top:after {
    display: block;
    content: '';
    width: 100%;
    padding-bottom: 36%; }
  .page-top svg {
    margin: var(--logo-margin); }
  .page-top p {
    font-size: var(--page-top-paragraph);
    line-height: 1.5;
    max-width: 58rem;
    margin: 0 auto; }

@media only screen and (min-width: 760px) {
  .page-top:after {
    padding-bottom: 28rem; } }

.screenshots {
  margin: -30% auto var(--screenshots-margin-bottom);
  padding: 0;
  position: relative;
  max-width: 76rem;
  width: calc(100% - 4rem); }
  .screenshots__laptop {
    position: relative;
    z-index: 1;
    pointer-events: none; }

.slide {
  position: absolute;
  left: 12.3%;
  right: 12.3%;
  top: 6.5%;
  opacity: 0;
  transition: opacity 1s ease;
  cursor: pointer; }
  .slide.active {
    opacity: 1; }
  .slide__label {
    position: absolute;
    left: 50%;
    bottom: 3rem;
    background: var(--blue);
    color: white;
    padding: .5rem 1rem;
    transform: translate(-50%);
    white-space: nowrap;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
    border-radius: .5rem; }

#slide2 .slide__label {
  background: #B65391; }

@media only screen and (min-width: 760px) {
  .screenshots {
    margin-top: -23rem; }
  .slide__label {
    padding: 1.3rem 3rem; } }

.explainer-text {
  margin: 0 0 5rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: center; }
  .explainer-text .column {
    max-width: 46rem;
    margin: 0 auto; }
  .explainer-text .column:first-child p {
    font-size: var(--explainer-text-lead);
    color: var(--blue); }

.key-features {
  background: #F7FAFD;
  padding: var(--key-features-padding);
  margin: 0 0 5rem; }
  .key-features__title {
    font-size: var(--key-features-title);
    color: var(--blue);
    margin: 0 0 5rem; }
  .key-features__list {
    list-style: none;
    margin: 0 -3rem;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center; }
    .key-features__list li {
      margin: 0 3rem 5rem;
      max-width: 28rem; }
    .key-features__list svg {
      display: block;
      margin: 0 auto 3.5rem; }

.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px; }

.holder {
  max-width: 102rem;
  margin: 0 auto;
  padding: 0 3rem; }

.text-align-c {
  text-align: center; }

img {
  max-width: 100%;
  height: auto; }

p,
li {
  font-size: 1.8rem;
  line-height: 1.5;
  margin: 0 0 1.6rem; }

.padding-b-5 {
  padding-bottom: 5rem; }

.footer-text {
  margin: var(--footer-text-margin);
  display: block; }

a {
  color: var(--blue);
  text-decoration: underline; }
  a:hover {
    text-decoration: none; }
